<template>
  <wrapper titulo="Saldos">
    <v-row v-if="listaAlumnos.length > 0">
      <v-col v-for="(alumno, index) in listaAlumnos" :key="index" cols="12" md="6">
        <v-card style="border-radius: 40px;" color="#eaf1f6" class="my-4">
          <v-card class="text-center d-flex flex-column justify-space-between align-center pa-4" color="transparent" flat>
            <v-avatar size="180px">
              <v-img v-if="alumno.refImagen" :src="alumno.refImagen" size="180px"/>
              <v-img v-else src="../../assets/nino.jpg" size="180px"/>
            </v-avatar>
          </v-card>

          <v-card-title class="text-center" style="word-break: normal!important; display: grid">
            <v-btn text large>{{ alumno.nombreCompleto }}</v-btn>
          </v-card-title>
          <v-card-text class="text-center">
            <v-btn v-if="!alumno.cafeterias" depressed color="teal" dark x-small class="font-weight-bold" @click="mostrarSaldos(alumno)">Ver saldo</v-btn>
          </v-card-text>
          <v-card-actions v-if="alumno.cafeterias">
            <v-list subheader color="transparent">
              <v-list-item  v-for="(cafeteria, indexCafeterias) in alumno.cafeterias" :key="indexCafeterias">
                <v-list-item-avatar>
                  <v-img  src="https://cdn.pixabay.com/photo/2019/08/27/04/18/store-icon-4433328_960_720.png"/>
                </v-list-item-avatar>

                <v-list-item-content>
                  <v-list-item-title> {{ cafeteria.nombre }} </v-list-item-title>
                  <v-list-item-subtitle v-if="alumno.cafeteria.id === cafeteria.id">
                    <v-chip x-small color="info">Predeterminada</v-chip>
                  </v-list-item-subtitle>
                  <v-list-item-subtitle class="font-weight-bold primary--text"> {{ cafeteria.saldoAlumno | moneda }} </v-list-item-subtitle>
                </v-list-item-content>

                <v-list-item-icon v-if="$usuario.esPadreDeFamilia">
                  <v-btn x-small depressed color="success" @click="abrirDialogAgregarSaldo(alumno, cafeteria)">Agregar saldo</v-btn>
                </v-list-item-icon>
              </v-list-item>
            </v-list>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>

    <v-row v-else align="center" justify="center" style="display: flex;justify-content: center;align-items: center;">
      <v-col cols="12" class="text-center" style="display: flex;justify-content: center;align-items: center;">
        <img src="../../assets/ni√±os_estado_vacio.svg" :style="$vuetify.breakpoint.smAndDown ? 'width:100%' : 'width:50%'"/>
      </v-col>
      <v-col cols="12" class="text-center">
        <h2 style="font-family: Nunito;" class="text--disabled">No hay alumnos agregados</h2>
      </v-col>
      <v-col cols="12" md="6" class="text-center">
        <v-btn rounded :to="{ name: 'Alumnos' }"><span style="font-family: Nunito;" class="primary--text">Ir a Alumnos</span></v-btn>
      </v-col>
    </v-row>

    <dialogo v-model="dialogAgregarSaldo"
             :title="alumnoParaOperar && alumnoParaOperar.cafeteria ?
             'Recarga de saldo para ' + alumnoParaOperar.cafeteria.nombre : ''" ancho="900">

      <v-form v-model="formDatosRecarga" lazy-validation ref="formDatosRecarga">
        <v-window v-model="step" class="my-5 py-5">
          <v-window-item :value="1">
            <v-row>
              <v-col cols="12" class="text-center">
                <h2 class="text--disabled">Seleccione el monto a recargar</h2>
              </v-col>
              <v-col v-for="(cantidad, index) in cantidades" :key="index" cols="12" md="4" class="text-center centrar">
                <v-card :class="cantidad.value === monto ? 'tarjeta_seleccionada centrar' : 'tarjeta_no_seleccionada centrar' " height="100"
                        width="200" outlined @click="seleccionarMonto(cantidad.value)" :dark="cantidad.value === monto">
                  <v-card-text>
                    <h1 :class="cantidad.value != monto ? 'blue--text' : ''">{{ cantidad.text |moneda }}</h1>
                  </v-card-text>
                </v-card>
              </v-col>
            </v-row>
          </v-window-item>

          <v-window-item :value="2">
            <v-row>
              <v-col cols="12" md="7">
                <v-card dark style="border-radius: 20px; background-color: #010d4c;
background-image: linear-gradient(43deg, #010d4c 0%, #0c72b7 46%, #03337d 100%);
">
                  <v-card-title class="mb-4">
                    <v-spacer/>
                    <img src="../../assets/mastercard.png" style="width: 80px!important;"/>
                    <img src="../../assets/visa.png" style="width: 70px!important;"/>
                  </v-card-title>
                  <v-card-text>
                    <v-row>
                      <v-col cols="12" class="py-0">
                        <h4>N√∫mero de tarjeta</h4>
                        <v-text-field v-model="numCard" filled dense height="20px" class="font-weight-bold"
                                      style="font-family: 'Space Mono', monospace;"
                                      :rules="reglasNumeroTarjeta" counter="16"
                                      @keyup="refreshInputData($event.target.value)"
                                      maxlength="16"/>
                      </v-col>
                      <v-col cols="6" class="py-0">
                        <h4>Fecha de vencimiento</h4>
                        <v-text-field v-model="expires" filled dense height="20px" :rules="reglasFechaCredito"
                                      :validate-on-blur="expires && expires.substr(0,2) <= 12" placeholder="mm/aa"
                                      :success="expires && expires.length === 5 && expires.substr(0,2) <= 12" v-mask="'##/##'"
                                      :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                                      :type="show1 ? 'text' : 'password'"
                                      name="input-10-1"
                                      @keyup="changeValueInfoUsuario()"
                                      @click:append="show1 = !show1"
                        />
                      </v-col>
                      <v-col cols="6" class="py-0">
                        <h4>CCV</h4>
                        <v-text-field v-model="ccv" filled dense height="20px" type="password" maxlength="3" :rules="reglasCardCode"/>
                      </v-col>
                      <v-col cols="12" class="py-0">
                        <h4>Nombre del titular</h4>
                        <v-text-field v-model="nameClient" filled dense height="20px" class="font-weight-bold" style="font-family: 'Space Mono';"
                                      :rules="nameRules"/>
                      </v-col>
                    </v-row>
                  </v-card-text>
                </v-card>
                <v-col v-if="this.datosTipoCard != null">
                  <v-checkbox v-if="this.cardSave" v-model="infoUsuario.deseaGuardarTarjeta" label="Tarjeta Guardada" :disabled="true" @click="dialogGuardarTarjeta = true"/>
                  <v-checkbox v-else v-model="infoUsuario.deseaGuardarTarjeta" label="Editar tarjeta"  @click="dialogGuardarTarjeta = true"/>
                </v-col>
                <v-col v-else>
                  <v-checkbox v-model="infoUsuario.deseaGuardarTarjeta" label="Guardar tarjeta" @click="dialogGuardarTarjeta = true"/>
                </v-col>

              </v-col>
              <v-col cols="12" md="4" class="centrar">
                <v-card class="text-center" flat>
                  <v-card-text>
                    <v-row>
                      <v-col cols="12">
                        <h2>Resumen</h2>
                      </v-col>
                      <v-col cols="12">
                        <h4>Recarga de saldo</h4>
                      </v-col>
                      <v-col cols="12">
                        <h1 class="blue--text text--darken-3">{{ monto |moneda }}</h1>
                      </v-col>
                      <v-col cols="12" class="centrar">
                        <v-checkbox v-model="requiresInvoice" label="Requiere factura"/>
                      </v-col>
                      <v-col cols="12">
                        <v-btn color="primary" class="font-weight-bold" large depressed @click="abrirDialogConfirmacion(true)">
                          Pagar <v-icon size="20">mdi-lock-outline</v-icon>
                        </v-btn>
                      </v-col>
                    </v-row>
                  </v-card-text>
                </v-card>
              </v-col>
            </v-row>
          </v-window-item>

        </v-window>

        <v-divider></v-divider>

        <v-card-actions>
          <v-btn v-if="step != 1" large color="light-blue darken-4" text @click="step--">
            <v-icon>mdi-arrow-left</v-icon> √Åtras
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn v-if="step != 2" color="light-blue darken-4" text large @click="step++">
            Ir al pago  <v-icon>mdi-arrow-right</v-icon>
          </v-btn>
        </v-card-actions>

        <!--          <v-col cols="12">
                    <v-text-field v-model="monto" :rules="nameRules" prefix="$" autofocus/>
                  </v-col>
                  <v-col cols="6">
                    <v-text-field v-model="numCard" :rules="nameRules" label="N√∫mero de tarjeta"/>
                  </v-col>
                  <v-col cols="6">
                    <v-text-field v-model="expires" :rules="nameRules" label="Expira"/>
                  </v-col>
                  <v-col cols="6">
                    <v-text-field v-model="ccv" :rules="nameRules" label="ccv"/>
                  </v-col>
                  <v-col cols="6">
                    <v-checkbox v-model="requiresInvoice" label="Requiere factura"/>
                  </v-col>-->
      </v-form>
      <!--      <template slot="actions">
             <v-btn text color="primary" plain @click="abrirDialogConfirmacion(true)">Pagar</v-btn>
            </template>-->

    </dialogo>
    <dialogo v-model="dialogDeseaAgregarSaldo"
             :title="'¬øDesea agregar saldo a cafeteria ' + (alumnoParaOperar.cafeteria ? alumnoParaOperar.cafeteria.nombre : '') + '?'">
      <template slot="actions">
        <v-btn text color="primary" plain @click="sendProcessPayment()">Aceptar</v-btn>
      </template>
    </dialogo>

    <dialogo v-model="dialogGuardarTarjeta" title="¬øDesea guardar tarjeta?">
      <template slot="actions">
        <v-btn text color="primary" plain @click="saveCardPayment()">Aceptar</v-btn>
      </template>
    </dialogo>
  </wrapper>
</template>

<script>
import CafeteriasService from "../../services/CafeteriasService";
import MontosServices from "../../services/MontosServices";
import UsuariosService from "../../services/UsuariosService";
import CargoSaldo from "../../models/CargoSaldo";
import AuthConst from "../../config/AuthConst";
import RecargaFacturacionService from "../../services/RecargaFacturacionService";
import ClientesInternetService from "@/services/ClientesInternetService";
import CryptoJS from 'crypto-js'

export default {
  name: "Saldo",
  data(){
    return{
      nameRules: [v => !!v || 'Este campo es requerido'],
      formDatosRecarga: false,
      dialogAgregarSaldo: false,
      alumnos: [],
      cargoSaldo:{},
      listaAlumnos: [],
      alumno: null,
      cafeteria: null,
      alumnoParaOperar: {},
      numCardHidden:null,
      monto: 0,
      numCard: null,
      expires: null,
      nameClient:"",
      ccv: null,
      saveCardCheck: false,
      requiresInvoice: false,
      dialogDeseaAgregarSaldo: false,
      taxDataEntity: {},
      datosTipoCard: null,
      cantidades: [
        { text: 50, value: 50 },
        { text: 100, value: 100 },
        { text: 150, value: 150 },
        { text: 200, value: 200 },
        { text: 500, value: 500 }
      ],
      reglasFechaCredito: [v => !!v || 'Este campo es requerido', v => (v && v.substr(0, 2) <= 12) || 'Ingrese un mes valido', v => (v && v.length == 5) || 'Ingrese fecha completa'],
      reglasNumeroTarjeta: [v => !!v || 'Este campo es requerido', v => (v && v.length == 16) || 'Ingrese 16 d√≠gitos'],
      reglasCardCode: [v => !!v || 'Este campo es requerido', v => (v && v.length == 3) || 'Ingrese 3 dig√≠tos'],
      step: 1,
      tipoCardValue: null,
      dialogGuardarTarjeta: false,
      years: [],
      key:null,
      meses: [
        { mes: '01' },
        { mes: '02' },
        { mes: '03' },
        { mes: '04' },
        { mes: '05' },
        { mes: '06' },
        { mes: '07' },
        { mes: '08' },
        { mes: '09' },
        { mes: '10' },
        { mes: '11' },
        { mes: '12' }
      ],
      anios: [],
      show1: false,
      show2: true,
      show3: false,
      show4: false,
      password: 'Password',
      infoUsuario: [],
      infoUsuarioCom: [],
      cardSave:false,
      afiliacion:null,
      nombreAfiliacion:null,
      ciaudadAfiliacion:null
    }
  },
  async created() {
    this.listaAlumnos = UsuariosService.getListaAlumnos(this.$usuario);
    this.anios = getAniosExpiracion();
    this.key = await RecargaFacturacionService.getConfiguracion("KEYENCRIP");


    await RecargaFacturacionService.getConfiguracionCliente("D4T4CL13Nt301", "D4T4CL13Nt302", "D4T4CL13Nt303").then(clienteConfig => {

      console.log("clienteConfig ", clienteConfig);

    }).catch(e => {
      console.log(e)
    })

  },
  watch: {
    dialogAgregarSaldo(val) {
      if (val) {
        let vm = this;
        this.cargarDatosCard(vm);
        this.loadInfoUsuario();
      }
    }
  },
  methods: {
    loadInfoUsuario(){
      const vm = this
      UsuariosService.getInfoFromFirebase(this.$usuario.email)
        .then(infoUsuario => {
          vm.infoUsuario = infoUsuario
          vm.infoUsuarioCom = JSON.parse(JSON.stringify(infoUsuario));
          vm.cardSave = true;

        })
        .catch(e => {
          console.log(e)
        })

    },
    saveInfoUsuario(){
      const vm = this
      UsuariosService.saveInfoFromFirebase(vm.infoUsuario)
        .then(infoUsuarioGuardada => {
          console.log(infoUsuarioGuardada)
        })
        .catch(e => {
          console.log(e)
        })

    },
    refreshInputData(value){
      this.numCardHidden = value;
      this.changeValueInfoUsuario();
    },
    changeValueInfoUsuario(){
      this.$set(this.infoUsuario, 'deseaGuardarTarjeta', false);
      this.cardSave = false;
    },
    async cargarDatosCard(vm){
      vm.datosTipoCard = null;
      this.$loader = true
      try {
        let numCardDes;
        await RecargaFacturacionService.getListDataCard(this.$usuario.id).then(datosCard => {

          vm.datosTipoCard = datosCard;
          numCardDes = this.decrypt(datosCard.tarjeta);
          this.expires = this.decrypt(datosCard.fecha);;

        }).catch(e => {
          console.log(e)
        });

        //vm.datosTipoCard = JSON.parse(JSON.stringify(datoCard));
        //let tarjetaEncrp = vm.datosTipoCard.map(a => a.tarjeta);
        //let fechaEncrp = vm.datosTipoCard.map(a => a.fecha);
        this.numCardHidden = numCardDes;
        this.numCard  = numCardDes.replace(/^(\d{0,0})(\d{0,12})/gi,"$1************");

      } catch (e) {
        console.log("e ", e)
        //this.$showAlerta('600', 'error', 'mdi-alert-circle', 'No se pudo cargar la informaci√≥n de la tarjeta.', true)
      }
      this.$loader = false

    },
    async mostrarSaldos (alumno){
      if (!alumno.cafeterias) {
        this.$loader = true
        try {
          this.$set(alumno, 'cafeterias', await CafeteriasService.getSaldoPorCafeteria(this.$usuario.idCliente, alumno.id))
        } catch (e) {}
        this.$loader = false
      }
    },
    abrirDialogConfirmacion(val){
      if (this.$refs.formDatosRecarga.validate()) {
        this.dialogDeseaAgregarSaldo = true
      }
    },
    abrirDialogAgregarSaldo(alumno, cafeteria){

      this.alumnoParaOperar = {alumno, cafeteria};
      this.$nextTick(() => (this.$refs.formDatosRecarga.reset()));
      this.dialogAgregarSaldo = true;

    },
    async sendProcessPayment(){

      /*const montoNumber = Number(this.monto)
      const numCard = this.numCardHidden;
      const expires = this.expires;
      const ccv = this.ccv;
      const nameClient = this.nameClient;
      const idUser = this.$usuario.id;
      const requiresInvoice = this.requiresInvoice;
      const cafeteria = this.alumnoParaOperar.cafeteria.id;
      const firstNum = numCard.charAt(0);
      let tipoCard;
      await RecargaFacturacionService.getTipoCard(firstNum).then(tipoCardData => {

        tipoCard = tipoCardData.tipo;

      }).catch(e => {
        tipoCard = "No se localizo"
        console.log(e)
      });
      console.log("montoNumber ",montoNumber);
      console.log("this.numCard ",numCard);
      console.log("this.expires ",expires);
      console.log("this.ccv ",ccv);
      console.log("this.nameClient ",nameClient);
      console.log("this.$usuario; " + this.$usuario.id);
      console.log("this.requiresInvoice " + requiresInvoice);
      console.log("this.alumnoParaOperar.cafeteria " + cafeteria);
      if(tipoCard != "No se localizo"){
        RecargaFacturacionService.sendProcessPayment(idUser, montoNumber,numCard, expires, ccv, nameClient, requiresInvoice, cafeteria, tipoCard).then(response =>{
          this.actualizarSaldo();
        }).catch(() => {
          this.$showAlerta('600', 'error', 'mdi-alert-circle', 'No se pudo actualizar la informaci√≥n, int√©nte m√°s tarde.', true)
        });
      }else{
        this.dialogGuardarTarjeta = false;
        this.$showAlerta('600', 'error', 'mdi-alert-circle', 'No se pudo actualizar la informaci√≥n, verifique el num. de tarjeta.', true)
      }*/

      const numCard = this.numCardHidden;
      const expires = this.expires;
      let mapForm = document.createElement("form");
      mapForm.target = "_self";
      mapForm.setAttribute("type", "hidden");
      mapForm.method = "POST"; // or "post" if appropriate
      mapForm.action = "https://via.banorte.com/secure3d/Solucion3DSecure.htm";

      let carNumInput = document.createElement("input");
      carNumInput.type = "text";
      carNumInput.name = "CARD_NUMBER";
      carNumInput.value = numCard;

      let carExpInput = document.createElement("input");
      carExpInput.type = "text";
      carExpInput.name = "CARD_EXP";
      carExpInput.value = expires;

      let carTypeInput = document.createElement("input");
      carTypeInput.type = "text";
      carTypeInput.name = "CARD_TYPE";
      carTypeInput.value = 'MC';


      let amountInput = document.createElement("input");
      amountInput.type = "text";
      amountInput.name = "AMOUNT";
      amountInput.value = '5';

      let merchantInput = document.createElement("input");
      merchantInput.type = "text";
      merchantInput.name = "MERCHANT_ID";
      merchantInput.value = '8847827';

      let merchanNameInput = document.createElement("input");
      merchanNameInput.type = "text";
      merchanNameInput.name = "MERCHANT_NAME";
      merchanNameInput.value = 'HAPIT LUNCH';

      let merchanCityInput = document.createElement("input");
      merchanCityInput.type = "text";
      merchanCityInput.name = "MERCHANT_CITY";
      merchanCityInput.value = 'CUAUTITLAN IZCALLI';


      let pathInput = document.createElement("input");
      pathInput.type = "text";
      pathInput.name = "FORWARD_PATH";
      pathInput.value = 'https://lunch.hapitcloud.dev';


      let certi3dInput = document.createElement("input");
      certi3dInput.type = "text";
      certi3dInput.name = "3D_CERTIFICATION";
      certi3dInput.value = '03';


      let referenceInput = document.createElement("input");
      referenceInput.type = "text";
      referenceInput.name = "REFERENCE3D";
      referenceInput.value = '030944';

      mapForm.appendChild(carNumInput);
      mapForm.appendChild(carExpInput);
      mapForm.appendChild(carTypeInput);
      mapForm.appendChild(amountInput);
      mapForm.appendChild(merchantInput);
      mapForm.appendChild(merchanNameInput);
      mapForm.appendChild(merchanCityInput);
      mapForm.appendChild(pathInput);
      mapForm.appendChild(certi3dInput);
      mapForm.appendChild(referenceInput);
      document.body.appendChild(mapForm);

      //let map = window.open("", "Map", "status=0,title=0,height=600,width=800,scrollbars=1");

      console.log('enviando submit 3dplus..');
      mapForm.submit();
    },
    async saveCardPayment() {

      if(this.infoUsuario.deseaGuardarTarjeta != this.infoUsuarioCom.deseaGuardarTarjeta){
        this.saveInfoUsuario();
      }

      this.$loader = true
      try {
        let vm = this;
        const numCard = this.encrypt(this.numCardHidden);
        const expires = this.encrypt(this.expires);
        const idUser = this.$usuario.id;
        const firstNum = this.numCardHidden.charAt(0);
        //const tipoCard =  JSON.parse(JSON.stringify(await RecargaFacturacionService.getTipoCard(firstNum)));

        let tipoCard;
        await RecargaFacturacionService.getTipoCard(firstNum).then(tipoCardData => {

          tipoCard = tipoCardData.tipo;

        }).catch(e => {
          tipoCard = "No se localizo"
          console.log(e)
        });

        if(tipoCard != "No se localizo"){
          RecargaFacturacionService.saveCardPayment(idUser, numCard, expires, tipoCard).then(response =>{

            this.dialogGuardarTarjeta = false;
            this.cargarDatosCard(vm);
            this.cardSave = true;
            this.$showAlerta('600', 'success', 'mdi-check-circle-outline', 'Datos guardados exitosamente.', true);
          }).catch(() => {
            this.$showAlerta('600', 'error', 'mdi-alert-circle', 'No se pudo actualizar la informaci√≥n, int√©nte m√°s tarde.', true)
          });
        }else{
          this.dialogGuardarTarjeta = false;
          this.$showAlerta('600', 'error', 'mdi-alert-circle', 'No se pudo actualizar la informaci√≥n, verifique el num. de tarjeta.', true)
        }
      } catch (e){
        this.dialogGuardarTarjeta = false;
        this.$showAlerta('600', 'error', 'mdi-alert-circle', 'No se pudo actualizar la informaci√≥n, int√©nte m√°s tarde.', true);
      }
      this.$loader = false
    },
    encrypt (src) {
      let keykey = this.decryptKey(this.key.valor);
      const passphrase = keykey;

      return CryptoJS.AES.encrypt(src, passphrase).toString()
    },
    decrypt (src) {
      let keykey = this.decryptKey(this.key.valor);
      const passphrase = keykey;

      const bytes = CryptoJS.AES.decrypt(src, passphrase)
      const originalText = bytes.toString(CryptoJS.enc.Utf8);
      return originalText

    },
    encryptKey (src) {
      const passphrase = "";
      return CryptoJS.AES.encrypt(src, passphrase).toString()
    },
    decryptKey (src) {

      const passphrase = "";
      const bytes = CryptoJS.AES.decrypt(src, passphrase)
      const originalText = bytes.toString(CryptoJS.enc.Utf8);
      return originalText

    },
    async actualizarSaldo(){

      if (this.$usuario.esPadreDeFamilia) {
        this.$loader = true
        const montoNumber = Number(this.monto)
        try {
          const cargoSaldo = new CargoSaldo({
            monto: montoNumber,
            clienteinternet: this.$usuario,
            alumno: this.alumnoParaOperar.alumno,
            cafeteria: this.alumnoParaOperar.cafeteria,
          })
          await MontosServices.agregarCargo(cargoSaldo)
          this.alumnoParaOperar.cafeteria.saldoAlumno += montoNumber
          localStorage.setItem(AuthConst.CURRENT_USER_KEY, JSON.stringify(this.$usuario))
          this.$showAlerta('600', 'success', 'mdi-check-circle-outline', 'Saldo agregado.', true);
        } catch (e) {
          this.$showAlerta('600', 'error', 'mdi-alert-circle', 'No se pudo actualizar el saldo', true);
          this.$router.go()
        }
        this.monto = null
        this.$loader = false
        this.dialogAgregarSaldo = false
        this.dialogDeseaAgregarSaldo = false
      } else {
        this.$showAlerta('600', 'warning', 'mdi-alert-circle', 'Este usuario no puede agregar saldo.', false);
      }

    },
    seleccionarMonto(cantidad){
      this.monto = cantidad
    }
  }
}
function getAniosExpiracion() {
  let anioActual = Number(new Date().getFullYear()) // A√±o actual
  // El array se llena con 10 numeros, se crea un nuevo array en base del a√±o actual y se le suma los a√±os con respecto al primer array
  return [...Array(11).keys()].map(n => getUltimosDigitosAnio(anioActual + n))
}
// Convierte el a√±o en String y obtiene los ultimos dos digitos
function getUltimosDigitosAnio(anio) {
  return ('' + anio).substr(2, 3)
}
</script>

