import AuthConst from '../config/AuthConst';
import http from "./http";

const getConfiguracion =(key) =>{
  let url = AuthConst.BASE_URL + '/recarga-facturacion/configuracion/' + key;
  return http.get(url)
}

const getConfiguracionCliente =(afiliacion, nombre, ciudad) =>{
  let url = AuthConst.BASE_URL + '/recarga-facturacion/cliente-config';
  return http.post(url,{afiliacion:afiliacion, nombre:nombre, ciudad:ciudad })
}

const sendProcessPayment = (idUser,montoNumber,numCard, expires, ccv, nameClient, requiresInvoice, cafeteria, tipoCard) => {
  let url = AuthConst.BASE_URL + '/recarga-facturacion/recarga';
  return http.post(url, {idUser:idUser, monto:montoNumber , numCard: numCard, expires:expires, ccv:ccv, nameClient:nameClient, requiresInvoice:requiresInvoice, cafeteria:cafeteria, tipoCard:tipoCard})
};

const saveCardPayment = (idUser,numCard, expires, tipoCard) => {
  let url = AuthConst.BASE_URL + '/recarga-facturacion/saveCard';
  return http.post(url, {idUser:idUser,tipoTarjeta:tipoCard, tarjeta: numCard, fecha:expires});
};
const getTipoCard = (num) => {
  let url = AuthConst.BASE_URL + '/recarga-facturacion/tipoTarjeta/' + num ;
  return http.get(url);
};

const getListDataCard = (idUser) => {
  let url = AuthConst.BASE_URL + '/recarga-facturacion/dataCard/' + idUser ;
  return http.get(url);
};

export default { sendProcessPayment, saveCardPayment, getTipoCard, getConfiguracion, getListDataCard, getConfiguracionCliente}

